            text: 'Nombre y apellido',
            align: 'center',
            value: 'nombreYapellido',
          },
          {
            text: 'Sexo',
            align: 'center',
            value: 'sexo',
          },
          {
            text: 'Número de hoja',
            align: 'center',
            value: 'numeroHoja',
          },
          {
            text: 'Número de historia',
            align: 'center',
            value: 'numeroHistoria',
          },
        ],
      }
    },
    computed: {
      ...mapState(['fichas']),
    },
  }
</script>
